/* Styles globaux pour la page d'upload */
.upload-container {
    text-align: center;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .upload-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .upload-input {
    margin-top: 10px;
    margin-bottom: 20px;
  }
  
  .upload-preview {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  
  .upload-preview img {
    width: 300px;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .upload-button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #2196f3;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  