/* Styles globaux */
.container {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
}

/* En-tÃªte */
.header {
  background-color: #4caf50;
  color: white;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
}

/* Section pour l'image dynamique */
.image-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container img {
  width: 60%;
  max-width: 500px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Bouton de navigation */
.button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.button:hover {
  background-color: #0b7dda;
}
